import React from 'react';
import goals_2019 from '../../../images/Messi_2019_goals.JPG'
import messi_2019 from '../../../images/Messi_2019.jpg'


const Messi_6 = () => (
    <div style={{textAlign: "center", padding: "10px 5%"}}>
        <h1>2019</h1>
        <img className="team_img" src={messi_2019} alt="" />
        <h2>2019 Goals (Club-Only)</h2>
        <img style={{width:"100%", height:"60px", maxWidth:"325px"}} src={goals_2019} alt="" />
        <h3 style={{textDecoration: "underline"}}>Major Achievements:</h3>
        <h4>La Liga</h4>
    </div>
);

export default Messi_6;